.modal-backdrop.show {
  z-index: 1;
}
.modal-dialog {
  z-index: 2;
}
.fadeIn-enter .modal-dialog {
    transform: translateY(-2vh);
}
.fadeIn-enter-active .modal-dialog {
    transform: translateY(0);
    transition: transform 500ms ease-out;
}
.fadeIn-exit .modal-dialog {
    transform: translateY(0);
}
.fadeIn-exit-active .modal-dialog {
    transform: translateY(-2vh);
    transition: transform 500ms ease-in;
}
